* {
  box-sizing: border-box; }

body {
 }  // -webkit-tap-highlight-color: $selection-color

// Basic Styles
html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px; }

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px; }

body {
  background: $color-white;
  font-size: rem(15);
  font-family: $font-opensans-r;
  color: $primary-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  &.overflow-hidden {
    overflow: hidden; } }

//  Typography
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: $primary-color;
  font-weight: 400;
  text-decoration: none;
  margin: 0 0 15px 0;
  font-family: $font-yeseva; }

.h1, h1 {
  font-size: rem(31);
  line-height: 1.05;
  letter-spacing: -1px;
  margin-bottom: 25px; }

.h2, h2 {
  font-size: rem(22);
  line-height: 1.14;
  margin-bottom: 20px;
  &.color--white {
    color: $color-white; } }

.h3, h3 {
  font-size: rem(16);
  line-height: 1.38; }

.h4, h4 {
  font-size: rem(14);
  line-height: 1.38; }

.h5, h5 {
  font-size: rem(13); }

.h6, h6 {
  font-size: rem(12); }

.small, small {
  font-size: rem(12);
  line-height: 1.2; }

p {
  line-height: 1.6;
  margin: 0 0 25px 0; }

em, i, .em, .i {
  font-style: normal;
  font-family: $font-opensans-i; }

strong, b, .strong, .b {
  font-weight: normal;
  font-family: $font-opensans-b; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

// Links
a, .link {
  color: $link-color;
  text-decoration: underline;
  outline: none;
  transition: color 0.35s ease, background-color 0.35s ease, border-color 0.35s ease;

  &:hover, &:focus, &:active {
    color: $link-color;
    text-decoration: none;
    outline: none; } }

a.link {
  &:hover, &:focus, &:active {
    text-decoration: none; }
  &--reverse {
    text-decoration: none;
    &:hover, &:focus, &:active {
      text-decoration: underline; } } }

.container {
  margin: 0 auto;
  // width: 1280px
  width: 100%;
  min-width: 320px;
  padding: 0 15px; }

/* text align */
.text {
  &-center, &--center {
    text-align: center !important; }
  &-left, &--left {
    text-align: left !important; }
  &-right, &--right {
    text-align: right !important; } }

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: $color-white;
  &__block {
    background: $color-orange;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
    position: absolute;
    top: 50%;
    left: 50%;
    color: $color-orange;
    text-indent: -9999em;
    font-size: 11px;
    animation-delay: -0.16s;
    transform: translate(-50%,-50%);
    &:before, &:after {
      background: $color-orange;
      animation: load1 1s infinite ease-in-out;
      width: 1em;
      height: 4em; }
    &:before, &:after {
      position: absolute;
      top: 0;
      content: ''; }
    &:before {
      left: -1.5em;
      animation-delay: -0.32s; }
    &:after {
      left: 1.5em; } }
  @-webkit-keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } }
  @keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em; }
    40% {
      box-shadow: 0 -2em;
      height: 5em; } } }
@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em lightskyblue; }
  40% {
    box-shadow: 0 2.5em 0 0 lightskyblue; } }
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em lightskyblue; }
  40% {
    box-shadow: 0 2.5em 0 0 lightskyblue; } }

/* img */
.img {
  &-responsive, &--responsive {
    display: block;
    height: auto;
    max-width: 100%; }

  &-round, &--round {
    border-radius: 50%; } }

/* section */
.section {
  padding: 75px 0;
  min-width: 320px;
  &__head {
    width: 100%;
    max-width: 945px;
    margin: 0 auto 35px;
    display: table;
    &::after, &::before {
      content: '';
      display: table-cell;
      background-image: linear-gradient($color-gray, $color-gray);
      background-size: 100% 1px;
      background-repeat: no-repeat;
      background-position: center; }
    &>div {
      display: table-cell;
      white-space: nowrap;
      width: 280px;
      padding: 0 10px;
      text-align: center; }
    .h1, h1 {
      margin: 0;
      position: relative;
      small {
        display: block;
        font-size: rem(14);
        font-family: $font-opensans-i;
        position: absolute;
        left: 0;
        right: 0;
        margin-top: 15px;
        a {
          color: $color-graydark;
          i {
            margin-right: 5px; } } } }
    &--white {
      .h1, h1 {
        color: $color-white;
        small {
          a {
            color: $color-white; } } } } }
  &__content {
    p {
      &:last-child {
        margin-bottom: 0; } }
    &--narrow {
      max-width: 700px;
      margin: 0 auto; }
    &--comment {
      max-width: 850px;
      margin: 0 auto; }
    &--hall2-1 {
      max-width: 945px;
      margin: 80px auto; }
    &--contacts {
      padding-top: 30px; } }
  &--map {
    padding-top: 40px; } }

.content {
  &--404 {
    background: #262627 url('../img/page404-background.png') top center no-repeat;
    min-height: 630px; } }

ul li {
  list-style-type: disc; }
ol li {
  list-style-type: decimal; }

/* bg */
.bg {
  &--pink {
    background-color: pink !important; }
  &--white {
    background-color: $color-white !important; }
  &--black {
    background-color: $color-black !important; } }

/* no padding no margin */
.no {
  &--p {
    padding: 0 !important;
    &-top {
      padding-top: 0 !important; }
    &-bottom {
      padding-bottom: 0 !important; }
    &-left {
      padding-left: 0 !important; }
    &-right {
      padding-right: 0 !important; } }

  &--m {
    margin: 0 !important;
    &-top {
      margin-top: 0 !important; }
    &-bottom {
      margin-bottom: 0 !important; }
    &-left {
      margin-left: 0 !important; }
    &-right {
      margin-right: 0 !important; } }
  &--b {
    background: none !important;
    &-image {
      background-image: none !important; } } }

/* icons */
.icon {
  display: inline-block;
  // &--sprite
 }  //   @include sprite($img-name)

/* color */
.color {
  &--black {
    color: $color-black !important; }
  &--white {
    color: $color-white !important; }
  &--primary {
    color: $primary-color !important; } }

