      bottom: 0;
      transform: rotate(-90deg); } } }

/*
 * * Collapse */

.hamburger--collapse {
  .hamburger-inner {
    top: auto;
    bottom: 0;
    transition-duration: 0.13s;
    transition-delay: 0.13s;
